.button {
    display: inline-block;
    padding: 10px 18px;
    margin: 12px;
    font-size: 16px;
    font-weight: bold;
    color: var(--app-button-text-color);
    background-color: var(--app-button-color);
    border: none;
    border-radius: 8px;
    cursor: pointer;
    text-align: center;
    text-decoration: none;
    transition: background-color 0.3s ease, color 0.3s ease;
}

.button:active {
    background-color: rgba(169, 199, 255, 0.2);
}

.button:disabled {
    color: var(--app-subtitle-text-color);
    background-color: var(--app-button-color);
    cursor: not-allowed;
}

.favorite {
    width: -webkit-fill-available;
    margin: 12px;
    padding: 12px;
}

.trigger {
    margin: 0px
}

.unsubscribe {
    margin: 0;
    padding: 6px 12px;
    font-size: 14px;
    background-color: var(--app-destructive-text-color);
}

.unsubscribe:active {
    background-color: rgba(255, 169, 169, 0.2);
}